.head-to-head-page{padding:2rem;width:100%;min-height:100vh;background:linear-gradient(135deg, #87CEEB, #ade8f4);display:flex;justify-content:center;align-items:flex-start;box-sizing:border-box}.comparison-card{width:100%;max-width:1000px;margin:0 auto;background:rgba(255,255,255,.95);border-radius:24px !important;box-shadow:0 10px 30px rgba(0,0,0,.1),0 1px 8px rgba(0,0,0,.05) !important;backdrop-filter:blur(10px);border:1px solid rgba(255,255,255,.2);overflow:hidden}.comparison-card:hover{box-shadow:0 15px 35px rgba(0,0,0,.12),0 3px 10px rgba(0,0,0,.08) !important;transform:translateY(-2px);transition:all .3s ease}.comparison-header{background:linear-gradient(135deg, #0077b6, #00b4d8) !important;padding:2rem !important;position:relative;overflow:hidden}.comparison-header::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:radial-gradient(circle at top right, rgba(255, 255, 255, 0.1), transparent);pointer-events:none}.comparison-header h3{color:#fff !important;text-align:center;margin:0;font-size:1.75rem;font-weight:700;letter-spacing:.5px;text-shadow:0 2px 4px rgba(0,0,0,.1)}.comparison-content{padding:2rem !important;background:linear-gradient(to bottom, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.98))}.team-selectors{display:grid;grid-template-columns:repeat(auto-fit, minmax(240px, 1fr));gap:1.5rem;margin-bottom:1.5rem;padding:1rem;background:rgba(255,255,255,.5);border-radius:16px;border:1px solid rgba(226,232,240,.8)}.team-selectors .select-wrapper{position:relative}.team-selectors .select-wrapper::after{content:"▾";position:absolute;right:1rem;top:50%;transform:translateY(-50%);color:#0077b6;font-size:1.2rem;pointer-events:none;transition:transform .2s ease}.team-selectors .select-wrapper:hover::after{transform:translateY(-50%) scale(1.1)}.team-selectors .select-wrapper select{width:100%;padding:.875rem 2.5rem .875rem 1rem;border-radius:12px;border:2px solid #e2e8f0;background:#fff;font-size:1rem;color:#1a202c;font-weight:500;appearance:none;cursor:pointer;transition:all .2s ease}.team-selectors .select-wrapper select:focus{outline:none;border-color:#00b4d8;box-shadow:0 0 0 3px rgba(0,180,216,.15)}.team-selectors .select-wrapper select:hover{border-color:#00b4d8}.team-selectors .select-wrapper select:disabled{background-color:#f8fafc;cursor:not-allowed;border-color:#e2e8f0}.team-selectors .select-wrapper select option{padding:.75rem}.compare-button-wrapper{display:flex;justify-content:center;margin-top:1.5rem}.compare-button-wrapper button{padding:.875rem 2.5rem;background:linear-gradient(135deg, #0077b6, #00b4d8);color:#fff;border:none;border-radius:12px;font-weight:600;cursor:pointer;transition:all .3s ease;min-width:160px;font-size:1rem;letter-spacing:.5px;box-shadow:0 4px 12px rgba(0,119,182,.2)}.compare-button-wrapper button:hover:not(:disabled){transform:translateY(-2px);box-shadow:0 6px 16px rgba(0,119,182,.25);background:linear-gradient(135deg, #0088cc, #00c2e6)}.compare-button-wrapper button:active:not(:disabled){transform:translateY(0);box-shadow:0 2px 8px rgba(0,119,182,.2)}.compare-button-wrapper button:disabled{opacity:.7;cursor:not-allowed;background:#94a3b8;box-shadow:none}.comparison-results{margin-top:3rem}.comparison-results h3{color:#0f172a;margin-bottom:2rem;text-align:center;font-size:1.5rem;font-weight:700;letter-spacing:.5px;position:relative}.comparison-results h3::after{content:"";position:absolute;bottom:-0.75rem;left:50%;transform:translateX(-50%);width:60px;height:3px;background:linear-gradient(to right, #0077b6, #00b4d8);border-radius:2px}.teams-comparison{display:grid;grid-template-columns:1fr 1fr;gap:2rem;background:rgba(255,255,255,.5);padding:1.5rem;border-radius:16px;border:1px solid rgba(226,232,240,.8)}@media(max-width: 768px){.teams-comparison{grid-template-columns:1fr}}.team-column h4{display:flex;align-items:center;justify-content:center;gap:.75rem;color:#0077b6;margin-bottom:1.5rem;font-size:1.25rem;font-weight:700;padding:1rem;background:#fff;border-radius:12px;box-shadow:0 2px 8px rgba(0,0,0,.05)}.team-column h4 img.team-crest{width:32px;height:32px;object-fit:contain}.match-result{background:#fff;padding:1.5rem;border-radius:16px;margin-bottom:1rem;border:1px solid rgba(226,232,240,.8);display:flex;flex-direction:column;gap:.75rem;transition:all .3s ease;box-shadow:0 2px 8px rgba(0,0,0,.03)}.match-result:hover{transform:translateY(-2px);box-shadow:0 6px 16px rgba(0,0,0,.06);border-color:#00b4d8}.match-result .match-date{color:#64748b;font-weight:500;font-size:.875rem;text-align:center}.match-result .match-competition{color:#0077b6;font-weight:600;font-size:.875rem;text-align:center;background:#f0f9ff;padding:.25rem .75rem;border-radius:8px;display:inline-block;margin:0 auto}.match-result .match-teams{color:#1e293b;font-weight:600;font-size:1.125rem;text-align:center;line-height:1.4}.match-result .match-score{color:#0077b6;font-weight:700;font-size:1.5rem;text-align:center;padding:.75rem;background:#f0f9ff;border-radius:10px;border:1px solid #e0f2fe}.error-message{color:#991b1b;text-align:center;padding:1.25rem;background-color:#fee2e2;border-radius:12px;margin:1.5rem 0;font-weight:600;border:1px solid #fecaca;animation:fadeIn .3s ease}.error-message::before{content:"⚠️ ";margin-right:.5rem}.no-matches{text-align:center;padding:1.5rem;background:#f8fafc;border-radius:12px;color:#64748b;font-weight:500;border:1px dashed #e2e8f0}@keyframes fadeIn{from{opacity:0;transform:translateY(-10px)}to{opacity:1;transform:translateY(0)}}.loading{opacity:.7;pointer-events:none}